    this.timeline_2_3 = gsap.timeline({paused: true})
    this.timeline_1_2
      .to('.slide .offer-sub', {duration: 1, x: -450, autoAlpha: 0}, '-=1')
      .to('.slide .offer-btn', {duration: 1, y: 250, autoAlpha: 0}, '-=1')
