      .to('.slide .offer-text', {duration: 1, x: -55, autoAlpha: 0}, '-=1')
      .to('.car__tesla .car-services', {duration: 1, autoAlpha: 0}, '-=1')
      .to('.home-container .offer-features', {duration: 1, y: 100, autoAlpha: 0}, '-=1')
      .to('.home-container .car__tesla', {duration: 1.75, xPercent: -136}, '-=1')
      .to('.home-container .animated__about-subtitle', {duration: 1.5, autoAlpha: 1, maxWidth: '220px'}, '-=1.75')
      .to('.home-container .animated__about-title', {duration: 1.5, autoAlpha: 1, maxWidth: '738px'}, '-=1.75')
      .to('.home-container .animated__about-text-1', {duration: 1.5, autoAlpha: 1, maxWidth: '383px'}, '-=1.75')
      .to('.home-container .animated__about-text-2', {duration: 1.5, autoAlpha: 1, maxWidth: '292px'}, '-=1.75')
      .to('.home-container .about-bg', {duration: 1.5, autoAlpha: 1}, '-=1.75')
      .fromTo('.home-container .about-return', {x: 100, autoAlpha: 0},{duration: 1.5, autoAlpha: 1, x: 0}, '-=1.75')
      .fromTo('.home-container .about-video', {y: 100, autoAlpha: 0},{duration: 1, autoAlpha: 1, y: 0}, '-=1.25')
      .to('.slide__2', {duration: 1, autoAlpha: 1, onComplete: () => {
        this.canAnimateSlide = true
      }}, '-=1.5')
    
    this.timeline_2_1
      .to('.slide__2', {duration: 1, autoAlpha: 0})
      .to('.slide .offer-sub', {duration: 1, x: -250, autoAlpha: 0}, '-=1')
      .to('.slide .offer-title', {duration: 1, x: -150, autoAlpha: 0}, '-=1')
      .to('.slide .offer-text', {duration: 1, x: -25, autoAlpha: 0}, '-=1')
      .to('.slide__1', {duration: 1, autoAlpha: 1, onComplete: () =>{
        this.canAnimateSlide = true
      }})
    
    this.timeline_2_3
      .to('.slide__2', {duration: 1, autoAlpha: 0})
      .to('.slide__3', {duration: 1, autoAlpha: 1, onComplete: () => {
